import React, { useState, useEffect } from 'react';
import { PlusCircle, Send, Pin, Edit, Download, Upload, Trash2 } from 'lucide-react';
import ReactMarkdown from 'react-markdown';
import remarkMath from 'remark-math';
import rehypeKatex from 'rehype-katex';
import 'katex/dist/katex.min.css';

export default function App() {
  const [channels, setChannels] = useState([]);
  const [currentChannel, setCurrentChannel] = useState(null);
  const [message, setMessage] = useState('');
  const [newChannelName, setNewChannelName] = useState('');
  const [selectedMessage, setSelectedMessage] = useState(null);

  useEffect(() => {
    const savedChannels = localStorage.getItem('chatApp');
    if (savedChannels) {
      const parsedChannels = JSON.parse(savedChannels);
      setChannels(parsedChannels);
      if (parsedChannels.length > 0) {
        setCurrentChannel(parsedChannels[0]);
      }
    }
  }, []);

  useEffect(() => {
    localStorage.setItem('chatApp', JSON.stringify(channels));
  }, [channels]);

  const createChannel = () => {
    if (newChannelName.trim() === '') return;
    if (channels.some(channel => channel.name === newChannelName)) {
      alert('A channel with this name already exists.');
      return;
    }
    const newChannel = { name: newChannelName, messages: [] };
    setChannels([...channels, newChannel]);
    setCurrentChannel(newChannel);
    setNewChannelName('');
  };

  const deleteChannel = (name) => {
    if (window.confirm('Are you sure you want to delete this channel?')) {
      const updatedChannels = channels.filter(channel => channel.name !== name);
      setChannels(updatedChannels);
      if (currentChannel.name === name) {
        setCurrentChannel(updatedChannels[0] || null);
      }
    }
  };

  const sendMessage = () => {
    if (message.trim() && currentChannel) {
      const updatedChannel = {
        ...currentChannel,
        messages: [...currentChannel.messages, { content: message, pinned: false }]
      };
      setChannels(channels.map(ch => ch.name === currentChannel.name ? updatedChannel : ch));
      setCurrentChannel(updatedChannel);
      setMessage('');
    }
  };

  const pinMessage = (index) => {
    const updatedMessages = [...currentChannel.messages];
    updatedMessages[index] = {
      ...updatedMessages[index],
      pinned: !updatedMessages[index].pinned
    };
    updateChannel(updatedMessages);
  };

  const editMessage = (index) => {
    const newContent = prompt('Edit message:', currentChannel.messages[index].content);
    if (newContent !== null) {
      const updatedMessages = [...currentChannel.messages];
      updatedMessages[index] = { ...updatedMessages[index], content: newContent };
      updateChannel(updatedMessages);
    }
  };

  const deleteMessage = (index) => {
    if (window.confirm('Are you sure you want to delete this message?')) {
      const updatedMessages = currentChannel.messages.filter((_, i) => i !== index);
      updateChannel(updatedMessages);
    }
  };

  const updateChannel = (updatedMessages) => {
    const updatedChannel = { ...currentChannel, messages: updatedMessages };
    setChannels(channels.map(ch => ch.name === currentChannel.name ? updatedChannel : ch));
    setCurrentChannel(updatedChannel);
    setSelectedMessage(null);
  };

  const exportAllHistory = () => {
    const dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(channels));
    const downloadAnchorNode = document.createElement('a');
    downloadAnchorNode.setAttribute("href", dataStr);
    downloadAnchorNode.setAttribute("download", "chat_history.json");
    document.body.appendChild(downloadAnchorNode);
    downloadAnchorNode.click();
    downloadAnchorNode.remove();
  };

  const importAllHistory = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = function(e) {
        try {
          const importedChannels = JSON.parse(e.target.result);
          if (Array.isArray(importedChannels) && importedChannels.every(ch => ch.name && Array.isArray(ch.messages))) {
            setChannels(importedChannels);
            setCurrentChannel(importedChannels[0] || null);
          } else {
            throw new Error('Invalid format');
          }
        } catch (error) {
          alert('Invalid file format. Please select a valid JSON file.');
        }
      };
      reader.readAsText(file);
    }
  };

  return (
    <div className="flex h-screen bg-gray-100">
      {/* Sidebar */}
      <div className="w-64 bg-gray-800 text-white flex flex-col">
        <div className="p-4">
          <h2 className="text-xl font-bold mb-4">Channels</h2>
          <div className="flex mb-4">
            <input 
              type="text" 
              value={newChannelName}
              onChange={(e) => setNewChannelName(e.target.value)}
              placeholder="New channel name" 
              className="flex-grow p-2 rounded-l text-black"
              onKeyPress={(e) => e.key === 'Enter' && createChannel()}
            />
            <button 
              className="bg-green-500 p-2 rounded-r"
              onClick={createChannel}
            >
              <PlusCircle size={20} />
            </button>
          </div>
        </div>
        <div className="flex-grow overflow-y-auto">
          <ul className="px-2 space-y-2">
            {channels.map(channel => (
              <li 
                key={channel.name} 
                className={`flex justify-between items-center p-2 rounded cursor-pointer ${currentChannel && channel.name === currentChannel.name ? 'bg-blue-600' : 'hover:bg-gray-700'}`}
                onClick={() => setCurrentChannel(channel)}
              >
                <span className="truncate flex-grow">{channel.name}</span>
                <button onClick={(e) => { e.stopPropagation(); deleteChannel(channel.name); }} className="ml-2">
                  <Trash2 size={16} />
                </button>
              </li>
            ))}
          </ul>
        </div>
        {/* Import/Export buttons */}
        <div className="p-4 bg-gray-900 space-y-2">
          <button onClick={exportAllHistory} className="w-full bg-purple-500 text-white p-2 rounded flex items-center justify-center">
            <Download size={20} />
            <span className="ml-2">Export All</span>
          </button>
          <label className="w-full bg-orange-500 text-white p-2 rounded cursor-pointer flex items-center justify-center">
            <Upload size={20} />
            <span className="ml-2">Import All</span>
            <input type="file" className="hidden" onChange={importAllHistory} accept=".json" />
          </label>
        </div>
      </div>

      {/* Main chat area */}
      <div className="flex-1 flex flex-col">
        <div className="bg-white shadow p-4">
          <h2 className="text-xl font-bold">{currentChannel ? currentChannel.name : 'Select a channel'}</h2>
        </div>
        <div className="flex-1 overflow-y-auto p-4 space-y-4">
          {currentChannel && currentChannel.messages.map((msg, index) => (
            <div 
              key={index} 
              className={`relative p-2 rounded ${msg.pinned ? 'bg-yellow-100 border-l-4 border-yellow-500' : 'bg-gray-200'}`}
              onClick={() => setSelectedMessage(selectedMessage === index ? null : index)}
            >
              <ReactMarkdown 
                remarkPlugins={[remarkMath]}
                rehypePlugins={[rehypeKatex]}
              >
                {msg.content}
              </ReactMarkdown>
              {selectedMessage === index && (
                <div className="absolute top-0 right-0 mt-2 mr-2 bg-white rounded shadow-lg p-2 flex space-x-2">
                  <button onClick={() => pinMessage(index)} className="text-yellow-500"><Pin size={16} /></button>
                  <button onClick={() => editMessage(index)} className="text-blue-500"><Edit size={16} /></button>
                  <button onClick={() => deleteMessage(index)} className="text-red-500"><Trash2 size={16} /></button>
                </div>
              )}
            </div>
          ))}
        </div>
        <div className="bg-gray-200 p-4">
          <div className="flex space-x-2">
            <input 
              type="text" 
              value={message} 
              onChange={(e) => setMessage(e.target.value)} 
              className="flex-grow p-2 rounded" 
              placeholder="Type your message... (Use $$ for LaTeX equations)"
              onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
            />
            <button onClick={sendMessage} className="bg-blue-500 text-white p-2 rounded flex items-center">
              <Send size={20} />
              <span className="ml-2">Send</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}